import React, { useState, useEffect } from "react";
import io from "socket.io-client";
import { BdsInput } from 'blip-ds/dist/blip-ds-react';
import "./Chat.css";
import Select from '../Select/index'
import PropTypes from 'prop-types'
import getQueue from "../../../api/blip/servicePanelQueue";
import getTunnel from "../../../api/blip/servicePanelTunnel";
import sendMessageTakeBlip from "../../../api/blip/servicePanelRouter"
import uuid from "../../../utils/guid";
import { showToast } from "../../../api/commomService";
let socket;


const BlipMessageComponent = () => {

  const [shortName] = useState(localStorage.getItem("plugin-message-shortname"));

  const [message, setMessage] = useState("");
  const [messageQueue, setMessageQueue] = useState("Enviar mensagem");
  const [templates, setTemplates] = useState([]);
  const [dataSubBot, setDataSubBot] = useState([]);
  const [token, setToken] = useState('');
  const [identifier, setIdentifier] = useState('');
  const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));

  useEffect(async () => {
  
    fnAuxSubBots()
}, []);



  const gravaMensagem = (event) => {
    event.preventDefault();
    setMessage(event.detail.value)
  };

  const sendMessageQueue = async (event) => {

    event.preventDefault();

    const resultQueue = await getQueue.service({
      method: "get",
      to: "postmaster@desk.msging.net",
      uri: "/monitoring/waiting-tickets?version=2",
      id: uuid(),
    }, token);

    const {
      resource: { items },
    } = resultQueue.data;

    for (const iterator of items) {
      const {
        resource: { originator },
      } = (
        await getTunnel.service({
          id: uuid(),
          to: "postmaster@tunnel.msging.net",
          method: "get",
          uri: `/tunnels/${iterator.customerIdentity}`,
        },  localStorage.getItem('plugin-message-router-key'))
      ).data;

      await sendMessageTakeBlip.service({
        id: uuid(),
        from: `${shortName}@msging.net`,
        to: originator,
        type: "text/plain",
        content: `${message}`,
      }, localStorage.getItem('plugin-message-router-key'));
      setMessageQueue(`Enviando mensagem para ${originator.split('@')[0]}`)
    }

    showMessage('Mensagem enviada com sucesso!', 'success', 'Notice!');
    setMessage("");
    setMessageQueue("Enviar mensagem")

  };

  const showMessage = (msg, type) => {

    setTimeout(() => {
      showToast({ type: type, message: msg })
    }, 100);

  }
  const fnAuxSubBots = () => {


    const arraySubBots = [];
    subBots.filter((bots) => { if (bots.flowId !== '') arraySubBots.push({ label: bots.longName, value: bots.longName }); })


    const template_list = arraySubBots.map((template) => ({
        id: template.label,
        value: template.value,
        label: template.label,
        selected: false
    }));
    setTemplates(template_list);
    setDataSubBot(arraySubBots);
}

const handleSetEventSubBot = async (event) => {

  const nameSubBot = event;

  const { identifier, accessKey } = subBots.filter((e) => e.longName === nameSubBot)[0];

  const KEY = 'Key ' + btoa(`${identifier}:` + atob(accessKey));

  setToken(KEY)
  setIdentifier(identifier)

}

  return (
    <>
      <div className="container w-100">

        <div className="col-7">
          <label>Chatbot de Atendimento:</label>
          <Select
            name="template"
            label="Selecione um bot:"
            placeholder="Selecione.."
            multiple={false}
            searchable={true}
            options={templates}
            selected={templates}
            onChange={($event) => handleSetEventSubBot($event)}

          />
     
        </div>
        <br></br>

      </div> <br></br>

      <div className="container w-100">

        <div className="col-7">
          <label>Mensagem:</label>
          <BdsInput
            label="Escreva uma mensagem para os clientes que estão na fila"
            value={message}
            onBdsChange={(event) => gravaMensagem(event)}
            isTextarea="true"
            rows={5}
            onKeyPress={(event) => (event.key === "Enter" ? sendMessage(event) : null)}
          />
          <br></br>
          <bds-button icon="send" size="short" onClick={sendMessageQueue} >{messageQueue}</bds-button>

        </div>
        <br></br>

      </div>

    </>
  )

};

BlipMessageComponent.propTypes = {
  shortName: PropTypes.string.isRequired
}


export { BlipMessageComponent };
